export default class CommonConstants {

  static get authorityMap() {
    const myMap = new Map();

    myMap.set("", 99);
    myMap.set("", 98);
    return myMap;
  }

  static get authorityResMap() {
    const myMap = new Map();

    myMap.set(99, "");
    myMap.set(98, "");
    return myMap;
  }

}



